import React from 'react';
import { Row, Col, Card, Form, Button, InputGroup, FormControl, DropdownButton, Dropdown } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";


import ErrorBody from '../../App/components/Reusables/ErrorBody';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import { validateDiscountCreation } from '../../validation/discount';
import { createDiscount } from '../../api/misc';

const Create = (props) => {

    const [submitting, setSubmittting] = React.useState(false)
    const [discountData, setDiscountData] = React.useState({
        name: '',
        type: '',
        rate: '',
        code: '',
    })
    const [errors, setErrors] = React.useState({});

    const amendErrorOf = (field) => {
        setErrors({ ...errors, [field]: '' })
    }



    const _discountDataChange = (e) => {
        amendErrorOf(e.target.name)
        setDiscountData({
            ...discountData,
            [e.target.name]: e.target.value
        })
    }


    const _submit = () => {

        const { isValid, errors } = validateDiscountCreation(discountData);

        if (!isValid) {
            console.log(errors);
            setErrors({ ...errors })
            return;
        };


        setSubmittting(true)


        //if no errros api call
        const requestJson = {
            "action": "add",
            "discount_type": discountData.type,
            "name": discountData.name,
            "rate": parseFloat(discountData.rate),
            "code": discountData.code,
        }
        console.log(requestJson);
        createDiscount(requestJson)
            .then((res) => {
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Discount created successfully!', background: '#dffff0' })
                    props.history.push('/discounts');
                } else {
                    setSubmittting(false)
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                setSubmittting(false)
                console.log(err)
            });
    }



    return (
        <Aux>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <h5>Create Discount</h5>
                            <hr />
                            <Row>
                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Discount Name*</Form.Label>
                                        <Form.Control name="name" value={discountData.name} onChange={_discountDataChange} type="text" placeholder="Discount Name" />
                                        <ErrorBody>{errors.name}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={6}>

                                    <Form.Group  >
                                        <Form.Label>Discount Type*</Form.Label>
                                        <Form.Control name="type" value={discountData.type} onChange={_discountDataChange} as="select">

                                            <option value='' disabled >Select...</option>
                                            <option value='percent'  >Percent</option>
                                            <option value='fixed'  >Fixed</option>
                                        </Form.Control>
                                        <ErrorBody>{errors.type}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Rate*</Form.Label>
                                        <Form.Control name="rate" value={discountData.rate} onChange={_discountDataChange} type="number" placeholder="Rate" />
                                        <ErrorBody>{errors.rate}</ErrorBody>
                                    </Form.Group>
                                </Col>


                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Code</Form.Label>
                                        <Form.Control name="code" value={discountData.code} onChange={_discountDataChange} type="text" placeholder="Code" />
                                        <ErrorBody>{errors.code}</ErrorBody>
                                    </Form.Group>
                                </Col>


                            </Row>
                            <div className="d-flex justify-content-end" >
                                <Button
                                    variant="warning"
                                    onClick={() => { props.history.push('/discounts') }}
                                >
                                    Cancel
                                    </Button>
                                <Button
                                    disabled={submitting}
                                    variant="primary"
                                    onClick={_submit}
                                >
                                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Submitting</> : 'Submit'}

                                </Button>
                            </div>

                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Aux>
    );
}

export default Create;
